const login = require('../../static/utils/utils').Login;
const query = require('../../static/utils/utils').Query;
const Query = new query();
const Login = new login();

import {sprintAdd} from '../../static/utils/api.js';
var _this;
export default {
	data() {
		return {
			userInfo:"",
			nowInProjectId:"",     //当前所在项目的projectId
			nowHadSprint:[],    
			sprintName:"",      //冲刺的序号.
			sprintTarget:""    //冲刺的目标
		}
	},
	onShow(){
	  _this = this;
	  uni.getStorage({
	  	key:"userInfo",
		success:(res)=>{
			console.log(res.data);
			let id = {
				id:res.data.id
			}
			return Query.findUser(id)
			.then(data=>{
				console.log("从数据库中返回的用户的信息",data);
				_this.userInfo = data.data;
				_this.getHadProjectSprint();   
			})
		 },
		 fail:(error=>{
			 uni.redirectTo({
			 	url:'../login/login' 
			 })
		 })
	  })
	},
	
	methods: {
		//先不管这个宽度
		// getSystem:function(){   //获取宽度，用来设置input出的长度
		// 	_this = this;
		// 	console.log("进来次函数")
		// 	uni.getSystemInfo({
		// 		success:(res)=>{
		// 			console.log(res)
		// 		   _this.width = res.windowWidth-70      //将剩余的作为input的宽度
		// 		   console.log(_this.width)
		// 		}
		// 	})
		// },
		
		addSprintOrder:function(e){
			this.sprintName = e.detail.value
		},
		addSprintTarget:function(e){
			this.sprintTarget = e.detail.value
		},
		
		//查询本项目已经有的冲刺。
		getHadProjectSprint:function(){   //拿到当前所在的项目的id，在storage中存放着  NowInProjectId
			uni.getStorage({
				key:"nowInProject",
				success:(res)=>{
					_this.nowInProjectId = res.data.projectId;
					 console.log("进来模拟数据")
					 
					  //根据projectId去查找
					 
					   
					//这里对数据进行模拟：    
					 
					_this.nowHadSprint = [{
						id:2,
						sprintName:"模拟名称2",
						sprintTarget:"冲呀2"
					},{
						id:3,
						sprintName:"模拟名称3",
						sprintTarget:"冲呀3"
					},
					{
						id:4,
						sprintName:"模拟名称3",
						sprintTarget:"冲呀3"
					}]
				},
				
				fail:(error)=>{
					uni.showToast({
						title:"获取失败",
						duration:1000,
						icon:'loading'
					})
				}
			})
		},
		
		
		addSprint:function(){
			_this = this;
			console.log("提交的数据",_this.sprintName,_this.sprintTarget)
			
			if(_this.sprintName&&_this.sprintTarget){
				uni.showLoading({
					title:"提交中",
					success:()=>{
						uni.request({
							url:sprintAdd,
							method:"POST",
							data:{
								projectId:_this.nowInProjectId,
								sprintName:_this.sprintName,
								sprintTarget:_this.sprintTarget
							},
							dataType:'json'
							})
							.then(res=>{
								console.log("添加冲刺返回的信息",res);
								uni.hideLoading();
								uni.showToast({
									icon:'none',
								    title:"添加成功",
									duration:1000
								})
								//_this.getHadProjectSprint()  //再次刷新调用这个获取该项目所有的冲刺
								
								//暂时的处理
								let item = {
									id:_this.nowHadSprint.length+1,
									projectId:_this.nowInProjectId,
									sprintName:_this.sprintName,
									sprintTarget:_this.sprintTarget
								}
								_this.nowHadSprint.push(item);
								
							})
							.catch(error=>{
								console.log(error)
								uni.showToast({
									title:"添加失败",
									icon:'none',
									duration:1000
								})
							})				
					 }
			    })
			}else{
				uni.showToast({
					duration:500,
					title:"请完善信息"
				})
			}
		}
	}
}
